@use "sass:map";

$media: (
	"xs": 0,
	"sm": 36rem,
	"md": 48rem,
	"lg": 62rem,
	"xl": 75rem,
	"xxl": 87.5rem,
);

@mixin respond($breakpoint-name) {
	@each $breakpoint, $size in $media {
		@if $breakpoint-name == $breakpoint {
			@media screen and (min-width: $size) {
				@content;
			}
		}
	}
}
